.container {
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
}

.button {
	display: inline-block;
	color: black;
	background: white;
	text-shadow: none;
	box-shadow: none;
	padding: 18px 30px;
	border: none;
	text-transform: uppercase;
	font-size: 13px;
	letter-spacing: .13em;
	border-radius: 0!important;
	cursor: pointer;
	transition: color .3s,background-color .3s;
}
.button:hover {
	background: black;
	color: white;
}
.button:disabled {
	background: red;
}

main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

main > * {
  margin-bottom: 10px;
}

